'use client';

import { useEffect, useState } from 'react';

type MomentStatus = 'needs_review' | 'approved' | 'rejected';

type RawMoment = {
  id: number | string;
  content_title?: string;
  season?: number | null;
  episode?: number | null;
  minute?: number | null;
  confidence?: number | null;
  quote?: string | null;
  source_subreddit?: string | null;
  source_url?: string | null;
  created_utc?: number | null;
  status?: MomentStatus | string | null;
};

interface MomentData {
  id: number;
  content_title: string;
  season?: number | null;
  episode?: number | null;
  minute?: number | null;
  confidence: number;
  quote?: string | null;
  source_subreddit?: string | null;
  source_url?: string | null;
  created_utc?: number | null;
  status: MomentStatus;
}

function normalizeMoment(raw: RawMoment | null | undefined): MomentData | null {
  if (!raw) return null;

  const numericId = typeof raw.id === 'number' ? raw.id : Number(raw.id);
  if (!Number.isFinite(numericId)) return null;

  const contentTitle = typeof raw.content_title === 'string' && raw.content_title.trim().length > 0
    ? raw.content_title
    : 'Untitled moment';

  const confidenceValue = typeof raw.confidence === 'number' ? raw.confidence : 0;
  const statusValue: MomentStatus = raw.status === 'approved' || raw.status === 'rejected'
    ? raw.status
    : 'needs_review';

  return {
    id: numericId,
    content_title: contentTitle,
    season: raw.season ?? null,
    episode: raw.episode ?? null,
    minute: raw.minute ?? null,
    confidence: confidenceValue,
    quote: raw.quote ?? null,
    source_subreddit: raw.source_subreddit ?? null,
    source_url: raw.source_url ?? null,
    created_utc: raw.created_utc ?? null,
    status: statusValue,
  };
}

function formatSeasonEpisode(season?: number | null, episode?: number | null) {
  const parts = [season, episode].filter((value) => typeof value === 'number' && Number.isFinite(value));
  return parts.length ? parts.join('x') : 'N/A';
}

function formatMinute(minute?: number | null) {
  if (typeof minute === 'number' && Number.isFinite(minute)) return String(minute);
  return 'N/A';
}

function formatConfidence(confidence: number) {
  if (!Number.isFinite(confidence)) return '0%';
  return `${Math.round(confidence * 100)}%`;
}

function confidenceColor(confidence: number) {
  if (confidence >= 0.8) return 'bg-green-900 text-green-200';
  if (confidence >= 0.5) return 'bg-yellow-900 text-yellow-200';
  return 'bg-red-900 text-red-200';
}

export default function ModerationTable() {
  const [moments, setMoments] = useState<MomentData[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchPendingMoments = async () => {
    try {
      setLoading(true);
      const response = await fetch('/api/moderation/pending');
      if (!response.ok) throw new Error(`Request failed with status ${response.status}`);
      const result = await response.json();
      const normalized = Array.isArray(result?.data)
        ? result.data.map((item: RawMoment) => normalizeMoment(item)).filter(Boolean) as MomentData[]
        : [];
      setMoments(normalized);
      setError(null);
    } catch (err) {
      const message = err instanceof Error ? err.message : 'Failed to fetch data';
      setError(message);
      console.error('Error fetching pending moments', err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchPendingMoments();
  }, []);

  const handleAfterDecision = (id: number, ok: boolean) => {
    if (!ok) return;
    setMoments((current) => current.filter((moment) => moment.id !== id));
  };

  const handleApprove = async (id: number) => {
    try {
      const response = await fetch('/api/moderation/approve', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id }),
      });
      handleAfterDecision(id, response.ok);
    } catch (err) {
      console.error('Error approving moment', err);
    }
  };

  const handleReject = async (id: number) => {
    try {
      const response = await fetch('/api/moderation/reject', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id }),
      });
      handleAfterDecision(id, response.ok);
    } catch (err) {
      console.error('Error rejecting moment', err);
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center p-8">
        <div className="text-lg">Loading moments...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="p-4 bg-red-900 border border-red-700 rounded-lg">
        <h3 className="font-semibold text-red-100">Error loading data</h3>
        <p className="text-red-200">{error}</p>
        <button
          onClick={fetchPendingMoments}
          className="mt-3 inline-flex items-center px-4 py-2 rounded bg-red-700 hover:bg-red-600"
        >
          Retry
        </button>
      </div>
    );
  }

  return (
    <div className="space-y-4">
      <div className="flex items-start justify-between gap-4">
        <div>
          <h2 className="text-xl font-semibold">Pending Reddit Moments</h2>
          <p className="text-sm text-neutral-400">
            Review community-sourced hook suggestions and approve or reject them.
          </p>
        </div>
        <button
          onClick={fetchPendingMoments}
          className="px-4 py-2 rounded bg-neutral-800 hover:bg-neutral-700 border border-neutral-700"
        >
          Refresh
        </button>
      </div>

      {moments.length === 0 ? (
        <div className="flex flex-col items-center justify-center gap-2 rounded-xl border border-neutral-800 bg-neutral-900 p-10 text-center">
          <h3 className="text-lg font-medium">No moments to review</h3>
          <p className="text-neutral-400">Approved or rejected items will appear here once new data arrives.</p>
        </div>
      ) : (
        <div className="overflow-x-auto">
          <table className="w-full border border-neutral-800 rounded-xl bg-neutral-900">
            <thead className="bg-neutral-800">
              <tr>
                <th className="p-3 text-left">Title</th>
                <th className="p-3 text-left">S/E</th>
                <th className="p-3 text-left">Min</th>
                <th className="p-3 text-left">Confidence</th>
                <th className="p-3 text-left">Quote</th>
                <th className="p-3 text-left">Source</th>
                <th className="p-3 text-left">Actions</th>
              </tr>
            </thead>
            <tbody>
              {moments.map((moment) => (
                <tr key={moment.id} className="border-t border-neutral-800 hover:bg-neutral-800/50">
                  <td className="p-3 font-medium text-neutral-100">{moment.content_title}</td>
                  <td className="p-3 text-neutral-200">
                    {formatSeasonEpisode(moment.season, moment.episode)}
                  </td>
                  <td className="p-3 text-neutral-200">{formatMinute(moment.minute)}</td>
                  <td className="p-3">
                    <span className={`px-2 py-1 rounded text-xs font-medium ${confidenceColor(moment.confidence)}`}>
                      {formatConfidence(moment.confidence)}
                    </span>
                  </td>
                  <td className="p-3 max-w-[320px]">
                    <div className="truncate text-neutral-300" title={moment.quote ?? undefined}>
                      {moment.quote || 'No quote provided'}
                    </div>
                  </td>
                  <td className="p-3 text-sm text-neutral-300">
                    <div className="flex flex-col gap-1">
                      <span>{moment.source_subreddit || 'Unknown subreddit'}</span>
                      {moment.source_url && (
                        <a
                          href={moment.source_url}
                          target="_blank"
                          rel="noopener noreferrer"
                          className="text-blue-400 hover:text-blue-300 underline"
                        >
                          View Post
                        </a>
                      )}
                    </div>
                  </td>
                  <td className="p-3">
                    <div className="flex gap-2">
                      <button
                        onClick={() => handleApprove(moment.id)}
                        className="px-3 py-1 rounded bg-emerald-600 hover:bg-emerald-500 text-sm font-medium"
                      >
                        Approve
                      </button>
                      <button
                        onClick={() => handleReject(moment.id)}
                        className="px-3 py-1 rounded bg-rose-600 hover:bg-rose-500 text-sm font-medium"
                      >
                        Reject
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
}

